import { NavLink } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { closeMenu, closeSearch, closeCart } from '../../../context/action/App';
import { setImage } from '../../../context/action/Product';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretDown } from '@fortawesome/free-solid-svg-icons';

interface State {
	App: any;
}

export const HeaderNavbar = () => {
	const { menu, cart, search } = useSelector((state: State) => state.App);
	const dispatch = useDispatch();
	const handleClose = () => {
		dispatch(setImage(0));
		if (menu || search || cart) {
			if (menu) {
				dispatch(closeMenu());
			}
			if (cart) {
				dispatch(closeCart());
			}
			if (search) {
				dispatch(closeSearch());
			}
		}
	};
	return (
		<nav className={menu ? 'navbar active' : 'navbar'}>
			<ul>
				<li>
					<NavLink onClick={handleClose} to="about">
						sobre mi
					</NavLink>
				</li>
				<li>
					<NavLink onClick={handleClose} to="portfolio">
						portafolio
					</NavLink>
				</li>
				<li>
					<NavLink onClick={handleClose} to="services">
						servicios
					</NavLink>
				</li>
				<li>
					<NavLink onClick={handleClose} to="blogs">
						blogs
					</NavLink>
				</li>
				<li>
					<NavLink onClick={handleClose} to="contacto">
						contacto
					</NavLink>
				</li>
				{/* 
        <li>
          <NavLink onClick={handleClose} to="#">ocaciones +</NavLink>
          <ul>
            <li>
              <NavLink onClick={handleClose} to="#">regalos para mujer</NavLink>
            </li>
            <li>
              <NavLink onClick={handleClose} to="#">regalos para hombre</NavLink>
            </li>
            <li>
              <NavLink onClick={handleClose} to="#">cumpleaños</NavLink>
            </li>
            <li>
              <NavLink onClick={handleClose} to="#">graduaciones</NavLink>
            </li>
            <li>
              <NavLink onClick={handleClose} to="#">intercambio de regalos</NavLink>
            </li>
            
          </ul>
        </li> */}
				{/* <li>
          <NavLink onClick={handleClose} to="#">compras al por mayor</NavLink>
        </li> */}
				{/* <li>
          <NavLink onClick={handleClose} to="contacto">Contacto</NavLink>
        </li> */}
			</ul>
		</nav>
	);
};
